*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
   height: 60px;
   color: rgb(0, 0, 0);
   background-color: #002D62;
   box-shadow: rgba(81, 16, 97, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;

}
.nav.dark{
    background-color: #575757;
}
.logo{
    margin-left: 20px;
    font-family: "Playwrite FR Moderne", cursive;
    color: white;
    font-size: 18px;
}
.menu-list{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.menu-list ul{
    display: flex;
    justify-content: space-around;
    align-items: center;
    gap: 20px;
    margin-right: 20px;
  
}
.menu-list ul li{
    list-style-type: none;
}
.menu-list ul li a {
    color: white;
    font-family: "Playwrite IT Moderna", cursive;
    cursor: pointer;
    text-decoration: none;
}
.menu-list ul li:hover{
transform: scale(1.3);
transition: all 0.3s ease-in-out;
}
.menu-list .icons{
    font-size: 27px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: white; 
    cursor: pointer;
    margin-right: 20px;
}
.modes{
    margin-right: 40px;
}
.menu-list .icons .burger{
    color: white;
    font-size: 36px;
    display: none;
    margin:5px 20px 7px 20px;
    margin: 0 10px 0 20px;
}

.crossbut{
    font-size: 30px;
    margin: 0 10px 0 20px;
  
    display: none;
}
@media screen and (max-width:980px){
    .nav .lists .hidelist{
        position: fixed;
        width: 50%;
        height: 100vh;
        top: 60px;
        right: -18px;
        box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
        background-color: #0E3386;
        display: flex;
        justify-content:flex-start;
        align-items: center;
        flex-direction: column;
        transition: all 0.5s ease-in-out;
        transform: translateX(103%);
    }
    .nav .lists .showlist{
        position: fixed;
        width: 50%;
        height: 100vh;
        top: 60px;
        right: -18px;
        background-color: #0E3386;
        display: flex;
        justify-content:flex-start;
        align-items: center;
        flex-direction: column;
        transition: all 0.5s ease-in-out;
        transform: translateX(1%);
    }
    .nav.dark .showlist,.nav.dark .hidelist{
        background-color: #46424f;
    }
    .nav ul li{
       padding-top: 50px;
        font-size: 18px;
    }
    .modes{
        margin-right: -1px;
    }
   
    .menu-list .icons .burger{
       font-size: 30px;
       display: block 
    }
    .crossbut,.burger{
        display: block;
    }
     
}