*{
    margin: 0;
    padding: 0;
}
.container{
    background-color: rgb(23, 27, 82);
    width: 100%;
}
.content {
    display: flex;
    align-items: center;
    flex-direction: column;
}
.container .box1{
    display: flex;
    justify-content: center;
    align-items: center;
    padding-bottom: 10px;
    background-color: rgb(23, 27, 82);
    height: 150px;
    width: 100%;
}
.container.dark .box1{
    background-color:#2f2b3a;
}
.box1 button{
    height: 45px;
    width: 140px;
    border-radius: 24px;
    margin-top: 10px;
    background-color: rgb(0, 255, 255);
    box-shadow: 0 0 5px cyan, 0 0 25px cyan;
    color: rgb(243, 49, 75);
    font-size: 18px;
    font-weight: 800;
    outline: none;
    border: none;
    cursor: pointer;
}
.box1 button:hover{
    box-shadow: 0 0 5px cyan, 0 0 25px cyan, 0 0 50px cyan, 0 0 100px cyan, 0 0 200px cyan;
}
.container .box2{
    height: 280px;
    background-color: rgb(74, 86, 167);
    display: flex;
    justify-content: space-around;
    color: white;
}
.container.dark .box2{
    background-color: #46424f;
}
.box2{
    padding-top: 20px;
}
.box2 li{
list-style-type: none;
padding: 5px;
cursor: pointer;
}
.box2 li:hover{
    background-color: #cec8c8;
    color: blue;

}
.soci{
    display: flex;
    gap: 10px;
    flex-direction: column;
    cursor: pointer;
}
.soci .lo{
    display: flex;
    gap: 10px;
    font-size: 30px;
}
.box3{
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    padding: 15px 0;
    font-size: 14px;
}
.container.dark .box3{
    background-color: #5c5c61;
}
.box3 p{
    padding-top: 15px;
    padding-left: 5px;
}

@media(max-width:425px){
    .box2{
        display: flex;
        flex-wrap: wrap;
       padding-bottom: 350px;
       background-color: aqua;
    }
    .soci{
        padding-top: 40px;
    }
    .box2 h2{
        font-size: 16px;
    }
}
@media(max-width:320px){
    .content h1{
        font-size: 20px;
    }
    .box2{
        padding-bottom: 500px;
    }
    .res{
        margin-top: 40px;
        margin-left: 25px;
    }
    .soci{
        margin-left: 5px;
    }
    .box3{
        padding-top: 20px;
    }
}
