.box{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding-bottom: 150px;
    transition: all 0.5s ease-in-out;
}
.box.dark{
    background-color: #1a1625;
    transition:all 0.5s ease-in-out;
}
.head{
    padding:30px 0 ;
    font-family: "Playwrite FR Moderne", cursive;
    color: rgb(129, 0, 189);
}
.box.dark .head{
    color: white;
}
.textbox{
    width: 900px;
    height: 180px;
}
.textbox textarea{
    height: 100%;
    width: 100%;
    padding: 10px;
    color: blue;
    outline: none;
    border: none;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    font-size: 17px;
}
.wordcount{
    display: flex;
    margin-top: 20px;
    font-size: 20px;
}
.box.dark .wordcount{
    color: white;
}
.wordcount .word{
    margin-right: 20px;
}
select{
    height: 40px;
    width: auto;
    background-color: rgb(98, 163, 238);
    border: none;
    outline: none;
    border-radius: 3px;
    font-size: 18px;
    margin-right: 20px;
    padding: 0 10px;
}
.box.dark .textbox textarea{
background-color: #575757;
color: white;
box-shadow: rgb(187, 184, 184) 0px 3px 8px;
}
.box.dark textarea::placeholder{
    color: rgb(221, 218, 218);
}
label{
    font-size: 30px;
    font-family: "Playwrite IT Moderna", cursive;
    color: rgb(129, 0, 189);
}
.box.dark label{
    color: white;
}
.buttonbox{
    margin: 30px 0;
}
button{
   color: white;
   height: 40px;
   width: 90px;
   border: none;
   outline: none;
   border-radius: 5px;
   font-size: 18px;
   margin: 20px;
   cursor: pointer;
}
 .cancel{
    background-color: red;
}
.cancel:hover{
    background-color: rgb(230, 30, 30);
}
 .convert{
    background-color: green;
}
.convert:hover{
    background-color: rgb(6, 161, 6);
}
.resultbox label {
display: flex;
justify-content: flex-end;
flex-direction: column;
top: 50px;
}
.resultbox label .copy{
    background-color: green;
    color: white;
    float: right;
    cursor: pointer;
}
.copy-text button:hover{
    background-color: rgb(5, 163, 5);
}

@media(max-width:980px){
    .textbox{
        width: 700px;
        height: 200px;
    }
    .textbox textarea{
        height: 100%;
        width: 100%;
    }
    
}
@media(max-width:768px){
    .textbox{
        width: 600px;
        height: 180px;
    }
    .textbox textarea{
        height: 100%;
        width: 100%;
    }
}
@media(max-width:425px)
{
   
    .textbox{
        width: 350px;
        height: 180px;
    }
    .textbox textarea{
        height: 100%;
        width: 100%;
    } 
    .buttonbox select{
        width: 125px;
        font-size: 12px;
        margin-left: 20px;
    }
    .convert,.cancel{
        width: 70px;
        height: 40px;
    }
    .cancel{
        margin-left: -10px;
    }
   
}
@media(max-width:375px){
    .textbox{
        width: 300px;
    }
    .buttonbox select{
        margin-left: 30px;
        margin-bottom: 5px;
    }
}
@media(max-width:360px){
    .textbox{
        width: 300px;
        margin-right: 10px;
    }
  select{
    height: 40px;
    color: aliceblue;
  }
    .buttonbox .cancel{
        margin-left: -15px;
      
    }
 
}
@media(max-width:384px){
    .head{
        font-size: 14px;
    }
.textbox{
    width: 300px;
    margin-left: 6px;
}
}
@media(max-width:320px){
    .textbox{
        width: 280px;
        margin-left: 12px;
    }
    .head{
        font-size: 12px;
    }
    .wordcount{
        font-size: 16px;
    }
    select{
        margin-left: -150px;
    }
    .convert{
        margin-left: -10px;
        width: 60px;
        font-size: 12px;
    }
    .cancel{
        margin-left: -140px;
        width: 60px;
        font-size: 12px;
    }
}